body {
  justify-content: stretch;
  align-items: stretch;
  margin: 0;
  display: flex;
  overflow: hidden;
}

#info {
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: absolute;
}

#logo {
  z-index: 100;
  text-align: center;
  color: #333;
  font-size: 24px;
  position: absolute;
  top: 10px;
  left: 40px;
}

#logo img {
  width: 150px;
  height: auto;
}

canvas, div {
  border: none;
  margin: 0;
  padding: 0;
}

.concert-one-regular {
  font-family: Concert One, serif;
  font-style: normal;
  font-weight: 400;
}

.ui-card {
  color: #fff;
  background-color: #fff;
  border: 4px solid #2f373f;
  border-radius: 8px;
  width: 350px;
  padding: 10px;
  font-family: Arial, sans-serif;
  display: none;
  position: absolute;
  top: 60px;
  right: 30px;
  box-shadow: 0 0 #0000004d;
}

.ui-header {
  color: #465cbb;
  background-color: #fff;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  font-family: Concert One, serif;
  font-size: 32px;
  font-weight: bolder;
  display: flex;
  position: relative;
}

.ui-header:after {
  content: "";
  background-color: #465cbb;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  display: inline-block;
}

.ui-content {
  text-align: justify;
  color: #fff;
  background-color: #465cbb;
  border-radius: 5px;
  height: 320px;
  margin-top: 5px;
  padding: 10px;
  font-family: Concert One, serif;
  font-size: 20px;
  overflow-y: scroll;
}

.ui-content::-webkit-scrollbar {
  width: 12px;
}

.ui-content::-webkit-scrollbar-track {
  background: #0000004d;
  border-radius: 6px;
  margin: 5px;
}

.ui-content::-webkit-scrollbar-thumb {
  background: linear-gradient(#666, #444);
  border: 2px solid #0000004d;
  border-radius: 6px;
}

.ui-content::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(#777, #555);
}

.ui-content {
  scrollbar-width: thin;
  scrollbar-color: #666 #0000004d;
}

.ui-button {
  background-color: #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  padding: 5px;
  display: flex;
}

.ui-button span {
  font-weight: bold;
}

.ui-button button {
  color: #fff;
  background-color: #465cbb;
  border: 0;
  border-radius: 5px;
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  padding: 5px;
  font-family: Concert One, serif;
  font-size: 20px;
  transition: background-color .5s, transform .2s;
}

.ui-button button:hover {
  background-color: #6d88ff;
  transform: scale(1.02);
}

@media (width <= 768px) {
  .ui-card {
    width: 400px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .ui-header {
    font-size: 24px;
  }

  .ui-header:after {
    width: 20px;
    height: 20px;
  }

  .ui-content {
    height: 250px;
    font-size: 16px;
  }

  .ui-button span, .ui-button button {
    font-size: 16px;
  }
}

@media (width >= 769px) and (width <= 1024px) {
  .ui-card {
    width: 400px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .ui-header {
    font-size: 28px;
  }

  .ui-header:after {
    width: 22px;
    height: 22px;
  }

  .ui-content {
    height: 280px;
    font-size: 18px;
  }

  .ui-button span, .ui-button button {
    font-size: 18px;
  }
}

