body {
    margin: 0;
    overflow: hidden;
    display: flex;
    align-items: stretch;
    justify-content: stretch;
}

#info {
    position: absolute;
    width: 100%;
    height: 100%;
    pointer-events: none;
    /* Agar tidak mengganggu interaksi dengan elemen 3D di bawahnya */
}

/* Posisi #logo di tengah atas */
#logo {
    position: absolute;
    top: 10px;
    left: 40px;
    /* left: 50%;
    transform: translateX(-50%); */
    z-index: 100;
    text-align: center;
    font-size: 24px;
    color: #333;
    /* Sesuaikan warna sesuai kebutuhan */
}

#logo img {
    width: 150px;
    /* Atur ukuran gambar sesuai kebutuhan */
    height: auto;
    /* Spasi antara gambar dan teks */
}

canvas,
div {
    margin: 0;
    padding: 0;
    border: none;
}

/* Container for the UI card */
.concert-one-regular {
    font-family: "Concert One", serif;
    font-weight: 400;
    font-style: normal;
}

.ui-card {
    width: 350px;
    top: 60px;
    right: 30px;
    padding: 10px;
}

.ui-card {
    background-color: #fff;
    border: 4px solid #2f373f;
    border-radius: 8px;
    font-family: Arial, sans-serif;
    position: absolute;
    display: none;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.3);
    color: white;
}

/* Header */
.ui-header {
    font-family: "Concert One", serif;
    background-color: #fff;
    color: #465cbb;
    padding: 5px 10px;
    font-weight: bolder;
    font-size: 32px;
    border-radius: 5px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.ui-header::after {
    content: "";
    width: 25px;
    height: 25px;
    background-color: #465cbb;
    border-radius: 50%;
    display: inline-block;
}

/* Content */
.ui-content {
    font-family: "Concert One", serif;
    font-size: 20px;
    text-align: justify;
    color: #fff;
    background-color: #465cbb;
    padding: 10px;
    margin-top: 5px;
    border-radius: 5px;
    height: 320px;
    overflow-y: scroll;
}

/* Custom Scrollbar Styles */
.ui-content::-webkit-scrollbar {
    width: 12px;
}

.ui-content::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 6px;
    margin: 5px;
}

.ui-content::-webkit-scrollbar-thumb {
    background: linear-gradient(to bottom, #666, #444);
    border-radius: 6px;
    border: 2px solid rgba(0, 0, 0, 0.3);
}

.ui-content::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(to bottom, #777, #555);
}

/* Firefox scrollbar styles */
.ui-content {
    scrollbar-width: thin;
    scrollbar-color: #666 rgba(0, 0, 0, 0.3);
}

/* Rewards section */
.ui-button {
    display: flex;
    align-items: center;
    margin-top: 10px;
    background-color: #fff;
    padding: 5px;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
}

.ui-button span {
    font-weight: bold;
}

.ui-button button {
    font-family: "Concert One", serif;
    font-size: 20px;
    color: #fff;
    background-color: #465cbb;
    padding: 5px;
    border-radius: 5px;
    border: 0px;
    width: 100%;
    height: fit-content;
    transition: background-color 0.5s, transform 0.2s ease;
    ;
}

.ui-button button:hover {
    background-color: #6d88ff;
    transform: scale(1.02);
}

@media (max-width: 768px) {
    .ui-card {
        width: 400px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .ui-header {
        font-size: 24px;
    }

    .ui-header::after {
        width: 20px;
        height: 20px;
    }

    .ui-content {
        font-size: 16px;
        height: 250px;
    }

    .ui-button span {
        font-size: 16px;
    }

    .ui-button button {
        font-size: 16px;
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    .ui-card {
        width: 400px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .ui-header {
        font-size: 28px;
    }

    .ui-header::after {
        width: 22px;
        height: 22px;
    }

    .ui-content {
        font-size: 18px;
        height: 280px;
    }

    .ui-button span {
        font-size: 18px;
    }

    .ui-button button {
        font-size: 18px;
    }
}